body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #1a1919;
}

input {
    color: grey;
    width: 50%;
    margin-left: 2px;
}

h3,
h2 {
    color: #fff;
}

.GifGrid {
    display: flexbox;
    padding: 3px;
    width: 265px;
    height: 265px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 18px;
}
